.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app_form {
 padding: 20px;
 position: fixed;
 bottom: 0;
 z-index: 1;
 background-color: #e9e9eb;
 width: 90%;
 margin: 20px;
}

.app_formControll {
  display: flex !important;
  flex-direction: row !important;
}

.app_input {
  flex: 1;
}

.app_iconButton {
  flex: 0;
}

.login-form {
  width: 400px;
  margin: 30px auto;
}
.login-form form {        
  margin-bottom: 15px;
  background: #f7f7f7;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  padding: 30px;
}
.login-form h2 {
  margin: 0 0 15px;
}
.form-control, .login-btn {
  border-radius: 2px;
}
.input-group-prepend .fa {
  font-size: 18px;
}
.login-btn {
  font-size: 15px;
  font-weight: bold;
  min-height: 40px;
}
.social-btn .btn {
  border: none;
  margin: 10px 3px 0;
  opacity: 1;
}
.social-btn .btn:hover {
  opacity: 0.9;
}
.social-btn .btn-secondary, .social-btn .btn-secondary:active {
  background: #507cc0 !important;
}
.social-btn .btn-info, .social-btn .btn-info:active {
  background: #64ccf1 !important;
}
.social-btn .btn-danger, .social-btn .btn-danger:active {
  background: #df4930 !important;
}
.or-seperator {
  margin-top: 20px;
  text-align: center;
  border-top: 1px solid #ccc;
}
.or-seperator i {
  padding: 0 10px;
  background: white;
  position: relative;
  top: -11px;
  z-index: 1;
}

img {
  margin: 20px auto;
  margin-left: 100px;
}

.input-css {
  padding: 10px;
  border: 1px;
  background-color: whitesmoke;
}

.btn-login {
  width: 400px;
  background-color: rgba(46, 46, 238, 0.897) !important;
  color: white !important;
}

.btn-create {
  width: 180px;
  background-color: rgb(12, 170, 12) !important;
  color: white !important;
  margin-left: 110px;
}

.link {
  color: rgb(103, 103, 185);
  margin-right: 125px;
}

.body {
  background: whitesmoke;
}